"use client";
import { useState, FormEvent, ChangeEvent } from 'react';
import { IconX, IconPhone, IconZoomIn, IconZoomOut, IconMaximize, IconMinus, IconPlus } from '@tabler/icons-react';

export default function ContactModal() {
  const [isOpen, setIsOpen] = useState(false);
  const [isFullscreen, setIsFullscreen] = useState(false);
  const [zoomLevel, setZoomLevel] = useState(1);
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    phone: '',
    pincode: ''
  });

  // Sample design image - replace with your actual image
  const designImage = "https://images.unsplash.com/photo-1599707254554-027aeb4deacd?q=80&w=2071&auto=format&fit=crop";

  const handleChange = (e: ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSubmit = (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    console.log('Form Data:', formData);
    alert('Thank you! We will call you back soon.');
    setIsOpen(false);
    resetModal();
  };

  const closeModal = () => {
    setIsOpen(false);
    resetModal();
  };

  const resetModal = () => {
    setIsFullscreen(false);
    setZoomLevel(1);
    setFormData({
      name: '',
      email: '',
      phone: '',
      pincode: ''
    });
  };

  const zoomIn = () => {
    setZoomLevel(prev => Math.min(prev + 0.25, 3));
  };

  const zoomOut = () => {
    setZoomLevel(prev => Math.max(prev - 0.25, 0.5));
  };

  const resetZoom = () => {
    setZoomLevel(1);
  };

  const openFullscreen = () => {
    setIsFullscreen(true);
  };

  const closeFullscreen = () => {
    setIsFullscreen(false);
    setZoomLevel(1);
  };

  // Prevent body scroll when modal is open
  if (typeof document !== 'undefined') {
    if (isOpen || isFullscreen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = 'unset';
    }
  }

  return (
    <>
      {/* Trigger Button */}
      <button
        onClick={() => setIsOpen(true)}
        className="bg-gradient-to-r from-blue-600 to-blue-700 hover:from-blue-700 hover:to-blue-800 text-white px-6 py-3 rounded-lg shadow-lg hover:shadow-xl transition-all duration-300 flex items-center gap-2"
      >
        <IconPhone size={20} />
        Get a Callback
      </button>

      {/* Main Modal */}
      {isOpen && !isFullscreen && (
        <div className="fixed inset-0 bg-black bg-opacity-60 backdrop-blur-sm flex items-center justify-center z-[9999] p-4">
          <div className="bg-white rounded-2xl shadow-2xl w-full max-w-2xl max-h-[90vh] overflow-hidden relative">
            
            {/* Close Button */}
            <button
              onClick={closeModal}
              className="absolute top-4 right-4 z-10 text-gray-400 hover:text-gray-600 transition-colors bg-white rounded-full p-1 shadow-lg"
            >
              <IconX size={24} />
            </button>

            <div className="flex flex-col lg:flex-row">
              
              {/* Image Section */}
              <div className="lg:w-1/2 p-6 bg-gray-50">
                <div className="relative bg-white rounded-xl shadow-inner overflow-hidden">
                  
                  {/* Zoom Controls */}
                  <div className="absolute top-3 left-3 z-10 flex gap-2">
                    <button
                      onClick={zoomOut}
                      disabled={zoomLevel <= 0.5}
                      className="p-2 bg-black/70 text-white rounded-full hover:bg-black/80 transition-all disabled:opacity-50 disabled:cursor-not-allowed"
                      title="Zoom Out"
                    >
                      <IconMinus size={16} />
                    </button>
                    <button
                      onClick={resetZoom}
                      className="px-3 py-2 bg-black/70 text-white rounded-full hover:bg-black/80 transition-all text-xs font-bold"
                      title="Reset Zoom"
                    >
                      {Math.round(zoomLevel * 100)}%
                    </button>
                    <button
                      onClick={zoomIn}
                      disabled={zoomLevel >= 3}
                      className="p-2 bg-black/70 text-white rounded-full hover:bg-black/80 transition-all disabled:opacity-50 disabled:cursor-not-allowed"
                      title="Zoom In"
                    >
                      <IconPlus size={16} />
                    </button>
                  </div>

                  {/* Fullscreen Button */}
                  <button
                    onClick={openFullscreen}
                    className="absolute top-3 right-3 z-10 p-2 bg-black/70 text-white rounded-full hover:bg-black/80 transition-all"
                    title="Open Fullscreen"
                  >
                    <IconMaximize size={16} />
                  </button>

                  {/* Image Container */}
                  <div className="h-64 lg:h-80 overflow-hidden flex items-center justify-center cursor-grab active:cursor-grabbing">
                    <img
                      src={designImage}
                      alt="Design Preview"
                      className="transition-transform duration-300 ease-out select-none"
                      style={{
                        transform: `scale(${zoomLevel})`,
                        maxWidth: 'none',
                        height: 'auto'
                      }}
                      draggable={false}
                    />
                  </div>
                </div>
                
                <p className="text-center text-sm text-gray-600 mt-3">
                  Click controls to zoom • Click fullscreen to view large
                </p>
              </div>

              {/* Form Section */}
              <div className="lg:w-1/2 p-6">
                
                {/* Header */}
                <div className="text-center mb-6">
                  <div className="w-12 h-12 bg-gradient-to-r from-blue-500 to-blue-600 rounded-full flex items-center justify-center mx-auto mb-3">
                    <IconPhone className="w-6 h-6 text-white" />
                  </div>
                  <h2 className="text-xl font-bold text-gray-800 mb-1">
                    Unlock exciting offers
                  </h2>
                  <p className="text-gray-600 text-sm">
                    by sharing your details
                  </p>
                </div>

                {/* Form */}
                <form onSubmit={handleSubmit} className="space-y-4">
                  
                  <input
                    type="text"
                    name="name"
                    placeholder="Name"
                    value={formData.name}
                    onChange={handleChange}
                    required
                    className="w-full px-4 py-3 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all duration-200 text-gray-700"
                  />

                  <input
                    type="email"
                    name="email"
                    placeholder="Email"
                    value={formData.email}
                    onChange={handleChange}
                    required
                    className="w-full px-4 py-3 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all duration-200 text-gray-700"
                  />

                  <input
                    type="tel"
                    name="phone"
                    placeholder="Phone"
                    value={formData.phone}
                    onChange={handleChange}
                    required
                    className="w-full px-4 py-3 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all duration-200 text-gray-700"
                  />

                  <input
                    type="text"
                    name="pincode"
                    placeholder="Pincode"
                    value={formData.pincode}
                    onChange={handleChange}
                    required
                    className="w-full px-4 py-3 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all duration-200 text-gray-700"
                  />

                  <button
                    type="submit"
                    className="w-full bg-gradient-to-r from-red-500 to-red-600 hover:from-red-600 hover:to-red-700 text-white font-semibold py-3 px-6 rounded-xl transition-all duration-300 shadow-lg hover:shadow-xl mt-6"
                  >
                    GET FREE QUOTE
                  </button>
                </form>

                <div className="text-center mt-3">
                  <p className="text-xs text-gray-500">
                    By submitting this form, you agree to our terms and conditions
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Fullscreen Modal */}
      {isFullscreen && (
        <div className="fixed inset-0 bg-black z-[10000] flex items-center justify-center">
          
          {/* Fullscreen Controls */}
          <div className="absolute top-6 left-6 right-6 flex justify-between items-center z-10">
            
            {/* Zoom Controls */}
            <div className="flex gap-3">
              <button
                onClick={zoomOut}
                disabled={zoomLevel <= 0.5}
                className="p-3 bg-white/20 backdrop-blur-sm text-white rounded-full hover:bg-white/30 transition-all disabled:opacity-50"
                title="Zoom Out"
              >
                <IconMinus size={20} />
              </button>
              <div className="px-4 py-3 bg-white/20 backdrop-blur-sm text-white rounded-full font-bold">
                {Math.round(zoomLevel * 100)}%
              </div>
              <button
                onClick={zoomIn}
                disabled={zoomLevel >= 3}
                className="p-3 bg-white/20 backdrop-blur-sm text-white rounded-full hover:bg-white/30 transition-all disabled:opacity-50"
                title="Zoom In"
              >
                <IconPlus size={20} />
              </button>
            </div>

            {/* Close Button */}
            <button
              onClick={closeFullscreen}
              className="p-3 bg-white/20 backdrop-blur-sm text-white rounded-full hover:bg-white/30 transition-all"
              title="Close Fullscreen"
            >
              <IconX size={20} />
            </button>
          </div>

          {/* Fullscreen Image */}
          <div className="w-full h-full flex items-center justify-center p-20 overflow-hidden">
            <img
              src={designImage}
              alt="Design Fullscreen"
              className="transition-transform duration-300 ease-out select-none cursor-grab active:cursor-grabbing"
              style={{
                transform: `scale(${zoomLevel})`,
                maxHeight: '100%',
                maxWidth: '100%',
                objectFit: 'contain'
              }}
              draggable={false}
            />
          </div>

          {/* Instructions */}
          <div className="absolute bottom-6 left-1/2 transform -translate-x-1/2 text-white/70 text-sm bg-black/50 px-4 py-2 rounded-full backdrop-blur-sm">
            Use zoom controls to magnify • ESC or X to close
          </div>
        </div>
      )}
    </>
  );
}
